.api-button{
    display: flex;
    flex-direction: row;
    float: right;
    margin-right:75px;
    background-color: #FFFFFF;
    fill: #FFFFFF;
    font-weight: 600;
    box-shadow: none;
    line-height: 1;
    font-size: 16px;
    padding: 10px;
    border-radius: 3px;
    text-align: center;
    transition: all .45s;
    border-style: none;
    color: black;
    font-family: "Montserrat", Sans-serif;
    font-size: 100%;
    display: flex;
    margin-right:14px;
    display: flex;

}

@media(max-width:560px){
    .api-button{
        float: left;
        display: block;
    }
}

.api h2{
    text-align: center;
    margin-bottom: 24px;
    text-transform: uppercase;
}

.API-Section{
    background: #E2E9F1;
}

#below-head{
    background-color: #e9e9e9;
    padding: 15px;
}

#below-head-values{
    background-color: #C4DFF4;
}

.api-table>thead>tr>th {
    padding: 15px;
    background-color: #202D62;
    color: white;
}

.api-table{
    margin-bottom: 34px;
}

.api-table>thead>tr>th{
    line-height: inherit;
}

.table>thead>tr>th{
    vertical-align: inherit;
}

.api-table>tbody>tr>th{
    padding: 15px;
}
.api-table>tbody>tr>td{
    padding: 15px;
}
