 .react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {

  margin: 0 0 10px;
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid black;
  position: relative;
  list-style: none;
  padding: 6px 42px;
  cursor: pointer;
  border-radius: 5px;
  margin-right:20px;
  margin-bottom: 14px;
}

.react-tabs__tab--selected {
  background: #3F89C8;
  color:white;
  border-radius: 5px;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

