/* Tab css */

.nav-t {
  margin-bottom: 1.25rem;
  margin-left: 0;
  list-style: none;
}

.nav-t>li>a {
  display: block;
}

.nav-tabs {
  *zoom: 1;
}

.nav-tabs:before,
.nav-tabs:after {
  display: table;
  content: "";
}

.nav-tabs:after {
  clear: both;
}

.nav-tabs>li {
  float: left;
}

.nav-tabs>li>a {
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: 0.1rem;
  line-height: 1rem;
}

.nav-tabs {
  border-bottom: 1px solid #3F89C8;
  margin-top: 30px;
}

.nav-tabs>li {
  margin-bottom: -1px;
}

.nav-tabs>li>a {
  padding-top: 8px;
  padding-bottom: 8px;
  line-height: 1.25rem;
  border: 1px solid transparent;
}

.nav-tabs>li>a:hover,
.nav-tabs>li>a:focus {
  border-color: #ddd #ddd #3F89C8;
}

.nav-tabs>.active>a,
.nav-tabs>.active>a:hover,
.nav-tabs>.active>a:focus {
  cursor: default;
  border: 1px solid #3F89C8;
  border-bottom-color: transparent;
  color: #fff;
  background: #3F89C8;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  text-align: center;
}

