.bg-myRed{
          background-color: crimson;
        }

        .content{
          padding: 12px;
          height: 3000px;
        }

        .coloring{
          color: black;
        }

        .navbar-brand{
                  height:auto;

        }

        .navbar-light .navbar-nav .nav-link{
                  color:black;
                  font-size: 18px;
                  font-weight: 600;
                  font-family: 'Montserrat', sans-serif;
                  color: black;
        }

        .navbar-light .navbar-nav .nav-link:hover{
                  color:#f89748;
        }

        .navbar-light .navbar-nav .nav-link.active{
          color:#f89748;
        }

        .right-aligned{
                  justify-content: flex-end;
        }

        .navbar{
                 margin-bottom: 0px;
                  margin-top: 10px;
                  padding: 0px;
                  background-color: white;
        }



      @media(max-width:768px){
        .navbar-brand{
          margin-right: 0px;
        }
      }

      .loans-menu{
        padding: 0.25rem 1.25rem;
      }

      .dropdown-item{
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-weight: 400;
      }
      .register-button{
        background: #E2D9D9;
        border-radius: 5px;
        padding: 10px 22px;
        color: #000;
        outline: none;
        cursor: pointer;
        margin-left: 40px;
      }

      @media (max-width:768px){
        .register-button{
          margin-left:0px;
          margin-bottom: 10px;
        }
      }
      @media (max-width:768px){
        .language-selector{
          margin-left:0px;
          margin-bottom: 10px;
        }
      }

      .navbar-nav{
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-weight: 600;
      }

      .language-selector{
        border-radius: 4px ;
        border: 1px solid grey;
        background: #fff;
        color: #000;
        outline: none;
        cursor: pointer;
        margin-left: 20px;
        font-size: 16px;
      }

      .dropdown-menu{
        width: auto;
      }